
import Loading from '@/components/loading/dataLoading'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'


const list = () => ['', '']

export default{
  name: 'goods_detail',
  data(){
    return {
      loading: true,//
      swiper_imgs: [],//
      swiper_index: 0,//
      cell_title: "",
      popup_model: false,//
      roundValue: 1,
      num_title: "",
      index: 0,
      click_tab: '',
      list2: list(),
      goods_name:'',
      max_price:'',
      min_price:'',
      sale_num:'',
      models:[],
      desc:'',
      evaluate:[],
      detailimgs:'',
      store_id:'',
      tel:'',
      trolley:'',
      choose_item:'',
      select_price:'',
      select_stock:'',
      click_mark:''
    }
  },
  mounted(){
    let self = this;
    this.$http.get('/api/goodsDetails').then((data) => {
      self.swiper_imgs = data.body.data.wximgs;
      self.goods_name = data.body.data.name;
      self.max_price = data.body.data.max_price;
      self.min_price = data.body.data.price;
      self.sale_num = data.body.data.sale_num;
      self.models = data.body.data.models;
      self.evaluate = data.body.data.evaluate;
      self.desc = data.body.data.desc;
      self.detailimgs = data.body.data.detailimgs;
      self.store_id = data.body.data.store_id;
      self.tel = data.body.data.tel;
      self.trolley = data.body.data.trolley;
    }, () => {
      console.log(2);
    });
    this.loading = false;
  },
  directives: {
    TransferDom
  },
  components: {
    Loading, Group, Cell, Actionsheet, Popup, Checker, CheckerItem, XNumber, Tab, TabItem, Swiper, SwiperItem
  },
  methods: {
    swiperChange(index){
      this.swiper_index = index
    },
    showActionsheet(mark){
      this.popup_model = true;
      this.click_mark=mark;
    },
    change (val) {
      console.log('change', val)
    },
    itemclick(val){
        this.choose_item=val;
        for(let i = 0;i<this.models.length;i++){
            if(val===this.models[i].name){
              this.select_stock=this.models[i].stock;
              this.select_price=this.models[i].price;
            }
        }
    },
    confirm_buy(){
      if(!this.choose_item){
          alert("");
      }else{
          alert(""+this.choose_item);
      }
    },
    toggleStar(){

    }
  }
}



